/*==============================================================
# Hero
==============================================================*/
	
.section-hero {
	overflow: hidden;
	position: relative;
	.section-background {
	    position: absolute;
	    z-index: -1;
	    height: 150%;
	    width: 100vw;
	    top: 0;
	    left: 0;
	    background-position: top center;
	    background-repeat: no-repeat;
	    background-size: cover;
	}
	.hero-content {
		padding: 30px 0 200px;
		text-align: center;
	}
	.header,
	.hero-content {
		position: relative;
		z-index: 5;
	}
	.header {
		svg {
			max-width: 240px;
		}
	}
	&.hero-overlay {
		&:after {
			content: "";
			display: block;
			position: absolute;
			top: 0px;
			left: 0px;
			width: 100%;
			height: 100%;
			z-index: 0;
			overflow: hidden;
			background: radial-gradient(transparent 25%, rgba(black, .5) 100%);
		}
	}
}