/*==============================================================
# Footer
==============================================================*/
	
.footer-section {
	background: #2c2c2c;
	text-align: center;
	padding: 35px 0 20px;
	margin: 25px 0 0;
	@include breakFirst(large) {
		margin: 35px 0 0;
		padding: 50px 0 30px;
	}
	@include breakFirst(wide) {
		margin: 50px 0 0;
		padding: 75px 0 50px;
	}
	.tonic-brand {
		.brand-logo {
			height: 70px;
			width: 70px;
			display: block;
			margin: 10px auto 15px;
			@include breakFirst(large) {
				margin: 20px auto 30px;
				height: 85px;
				width: 85px;
			}
			@include breakFirst(wide) {
				width: 100px;
				height: 100px;
			}
			svg {
				fill: $white;
				display: inline-block;
				width: 100%;
				height: 100%;
			}
		}
		p {
			font-size: .75em;
			@include breakFirst(large) {
				font-size: .85em;
			}
			@include breakFirst(wide) {
				font-size: 1em;
			}
		}
		a {
			color: $white;
		}
	}
	p {
		magin: 0;
	}
	.tonic-brand {
		color: $white;
		padding: 25px 0 55px;
		@include breakFirst(large) {
			padding: 45px 0 75px;
		}
		@include breakFirst(wide) {
			padding: 115px 0 195px;
		}
	}
	.intro {
		font-weight: 700;
		color: $white;
	}
	.disclaimer {
		color: $gray;
		font-size: .75em;
		@include breakFirst(large) {
			font-size: .85em;
		}
	}
}