/*==============================================================
# Packery
==============================================================*/

.inner-grid-half {
	width: 50%;
	float: left;
}
.grid-item { 
	width: 25%;
	&.column-width-full {
		width: 100%;
	}
	&.column-width-half {
		width: 100%;
		@include breakFirst(medium) {
			width: 50%;
		}
	}
	&.column-width-third {
		width: 100%;
		@include breakFirst(medium) {
			width: 33.33333%;
		}
		@include breakFirst(large) {
			width: 33.33333%;
		}
	}
	.grid-item-container {
		/*background: $white;*/
		padding: 10px;
		display: flex;
		height: 100%;
		position: relative;
		&.dark-background {
			padding: 20px;
			color: $white;
			svg {
				fill: $white;
			}
		}
		/*&:hover {
			box-shadow: inset 0 0 1px $gray;
		}*/
		&.container-width-half {
			width: 100%;
			float: left;
			align-items: flex-end;
			@include breakFirst(490px) {
				width: 50%;
			}
		}
		&:after {
			content: "";
			position: absolute;
			top: 10px;
			left: 10px;
			right: 10px;
			bottom: 10px;
			z-index: 1;
			border-radius: 4px;
			overflow: hidden;
		}
		&.container-height-large {
			min-height: 355px;
			max-height: 660px;
			padding-top: 175%;
			align-items: flex-end;
			&.justify-content {
				flex-direction: column;
    			justify-content: space-between;
			}
			@include breakFirst(medium) {
				min-height: 648px;
				padding-top: 20px;
			}
			@include breakFirst(large) {
				min-height: 848px;
			}
			@include breakFirst(wide) {
				min-height: 948px;
			}
			.name {
				@include breakFirst(medium) {
					font-size: 3rem;
				}
			}
		}
		&.container-height-medium {
			min-height: 355px;
			max-height: 660px;
			padding-top: 175%;
			align-items: flex-end;
			&.justify-content {
				flex-direction: column;
    			justify-content: space-between;
			}
			@include breakFirst(490px) {
				padding-top: 55%;
				min-height: 445px;
			}
			@include breakFirst(medium) {
				padding-top: 20px;
				min-height: 295px;
			}
			@include breakFirst(large) {
				min-height: 395px;
			}
			@include breakFirst(wide) {
				min-height: 445px;
			}
			&.square-ratio {
				@media (max-width: 490px) {
					height: 0;
					padding-top: 100%;
				}
			}
		}
		&.container-height-small {
			min-height: 280px;
			align-items: flex-end;
		}
		&.red-gradient-overlay {
			color: $white;
			svg {
				fill: $white;
			}
			&:after {
				 background: linear-gradient(transparent 25%, #fd2d2d 100%);
			}
		}
		&.blue-gradient-overlay {
			color: $white;
			svg {
				fill: $white;
			}
			&:after {
				 background: linear-gradient(transparent 65%, #3f82cf 100%);
			}
		}
		&.yellow-gradient-overlay {
			color: $white;
			svg {
				fill: $white;
			}
			&:after {
				 background: linear-gradient(transparent 65%, #dfe422 100%);
			}
		}
		&.purple-gradient-overlay {
			color: $white;
			svg {
				fill: $white;
			}
			&:after {
				 background: linear-gradient(transparent 65%, #772B90 100%);
			}
		}
		&.teal-gradient-overlay {
			color: $white;
			svg {
				fill: $white;
			}
			&:after {
				 background: linear-gradient(transparent 65%, #12E9DA 100%);
			}
		}
		iframe {
			height: 345px;
			@include breakFirst(wide) {
				min-height: 400px;
			}
		}
		.icon {
			width: 25px;
			height: 25px;
			display: inline-block;
			vertical-align: middle;
			svg {
				display: inline-block;
				float: left;
				width: 100%;
				height: 100%;
			}
		}
	}
	.grid-item-content {
		padding: 10px;
		width: 100%;
		position: relative;
    	z-index: 2;
	}
	.grid-item-suport-content {
	    position: absolute;
	    z-index: 1000;
	    &.bottom-right-support {
			bottom: 20px;
			right: 40px;
	    }
	    &.top-left-support {
			top: 40px;
			left: 40px;
	    }
	}
	.grid-item-background {
		background-size: cover;
		background-position: top center;
		background-repeat: no-repeat;
		position: absolute;
		top: 10px;
		left: 10px;
		right: 10px;
		bottom: 10px;
		z-index: 0;
		border-radius: 4px;
		overflow: hidden;
		@include breakFirst(460px) {
			background-position: center;
		}
		&.black-background {
			background-color: $black;
		}
		&.contain-background {
			background-size: contain;
			background-position: center;
		}
		&.position-bottom-center {
			background-position: bottom center;	
		}
	}
	h1, h2, h3, h4, h5, h6,  p {
		margin: 0;
		font-weight: 700;
	}
	h3 {
		font-size: 18px;
		&.label {
			/*display: none;*/
			font-size: .75em;
			text-transform: uppercase;
			margin-bottom: 10px;
		}
		&.desciption {
			font-size: .75em;
			margin: 10px 0;
			position: relative;
			&:before {
				content: "";
				display: block;
				width: 200px;
				height: 10px;
				background: #f88d15;
				margin: 20px 0;
			}
		}
	}
	h4 {
		font-size: 24px;
		&.name {
			/*display: none;*/
			margin-bottom: 15px;
		}
	}
	.details {
		/*display: none;*/
	}
	.grid-item-instruction {
		position: absolute;
		right: 50%;
		top: -60px;
		z-index: 50;
		transform: translate(50%, 0%);
		@include breakFirst(medium) {
			top: -40px;
			right: -40px;
			transform: translate(0%, 0%);
		}
	}
}