/*==============================================================
# Header
==============================================================*/
	
.header {
	.brand-wrapper {		
		display: table;
		width: 100%;
		text-align: center;
	}
	.brand-logo,
	.brand-message {
		display: block;
		@include breakFirst(medium) {
			display: table-cell;
			vertical-align: middle;
		}
	}
	.brand-message {
		color: $neonGreen;
		text-shadow: 2px 2px 0 black;
		text-transform: uppercase;
		@include breakFirst(medium) {
			text-align: right;
		}
	}
}