/*==============================================================
# Promotions
==============================================================*/

.promo-block {
	h3 {
		font-size: 18px;
	}
}
.hero-promo {
	margin-top: -100px;
	.promo-block {
		position: relative;
		background: $white;
		padding: 10px;
		border-radius: 10px;
		float: left;
		width: 100%;
		&:after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: calc(100% - 110px);
			box-shadow: 0px 10px 30px 10px $grayLight;
			border-radius: 10px;
			z-index: -1;
		}
	}
	.promo-column {
		display: inline-block;
		vertical-align: top;
		padding: 10px;
		/*text-align: center;*/
		@include breakFirst(medium) {
			float: left;
		}
		a {
			display: block;
		}
		img {
			width: 100%;
		}
		&.column-one {
		    width: 100%;	
		    h3 {
		    	margin-bottom: 10px;
		    }
		    img {
		    	display: none;
		    }
			@include breakFirst(medium) {
				width: 22%;
				text-align: left;
				h3 {
					margin-bottom: 1.61616rem;
				}
				img {
					width: 75%;
					display: block;
				}
			}
			@include breakFirst(large) {
				width: 20%;
				img {
					width: 85%;
				}
			}
			@include breakFirst(wide) {
				width: 17%;
				img {
					width: 100%;
				}
			}
		}
		&.column-two {
			 width: 100%;
			/*vertical-align: bottom;*/
			@include breakFirst(medium) {
				width: 46%;
			}
			@include breakFirst(large) {
				width: 48%;
			}
			@include breakFirst(wide) {
				width: 42%;
			}
		}
		&.column-three {
			width: 100%;
			a {
				max-width: 275px;
				margin: 0 auto;
			}
			@include breakFirst(medium) {
				width: 30%;
				text-align: right;
				a {
					max-width: 100%;
				}
			}
			@include breakFirst(large) {
				h3 {
					margin-right: 25px;
				}
			}
			@include breakFirst(wide) {
				width: 34%;
				h3 {
					margin-right: 65px;
				}
				img {
					float: right;
					max-width: 360px;
					margin: 0 auto;
					display: block;
				}
			}
		}
	}
}
.customer-promo {
	padding: 80px 0 20px;
}
.bus-promo {
	position: relative;
	&:before {
		content: "";
		display: block;
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 215%;
		background: linear-gradient(white 0%, rgba($gray, .2) 50%, white 100%);
		transform: translateY(-50%);
		z-index: -1;
	}
	&:after {
		content: "";
		display: block;
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 215%;
		background-image: url("../img/backgrounds/gradient-pattern.png");
		background-position: center;
		transform: translateY(-50%);
		z-index: -2;
	}
	img {
		display: block;
		max-width: 70%;
		position: relative;
		z-index: 10;
	    margin: -50px auto -50px;
	    @include breakFirst(medium) {
			margin: -100px auto -100px;
	    }
	}
}
.nike-promo {
	.promo-block {
		max-width: 1400px;
		width: 90%;
		margin: -6% auto 0;
		position: relative;
		z-index: -1;
		@include breakFirst(large) {
			margin: -55px auto 0;
		}
		@include breakFirst(wide) {
			margin: -75px auto 0;
		}
	}
	img {
		width: 100%;
		&.desktop-image {
			display: none;
			@include breakFirst(625px) {
				display: block;
			}
		}
		&.mobile-image {
			display: block;
			@include breakFirst(625px) {
				display: none;
			}
		}
	}
}
.alexa-promo {
	margin: 40px 0 0;
	.promo-block {
		padding: 10px;
		border-radius: 10px;
	}
	.promo-column {
		display: inline-block;
		vertical-align: middle;
		padding: 10px;
		text-align: center;
		width: 100%;
		@include breakFirst(medium) {
			text-align: left;
		}
		&.column-one {
			@include breakFirst(medium) {
				width: 65%;
			}
		}
		&.column-two {
			@include breakFirst(medium) {
				width: 30%;
			}
		}
	}
}
.form-promo {
	padding: 10px;
	h4 {
		color: #f210a7;
		text-transform: uppercase;
		font-weight: 700;
		font-size: 1em;
		margin-bottom: 5px;
	}
	p {
		color: lighten($grayDark, 30);
	}
	.form-wrapper {
		float: left;
		width: 100%;
		margin: 20px 0;
		box-shadow: 0px 10px 20px 5px $grayLight;;
	}
}
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: none;
	border-radius: 0px;
	cursor: pointer;
	line-height: 1;
	appearance: none;
	background: linear-gradient(to right, #f210a7, #a730d6, #a730d6, #f210a7);
	background-size: 400% 400%;
	background-position: 0% 0%;
	padding: 15px 40px;
	margin: 0;
	color: white;
	font-weight: 700;
	font-size: .85em;
	display: block;
	float: left;
	height: 50px;
	width: 165px;
	transition: all .4s $cbTrans;
	@include breakFirst(medium) {
		width: 30%;
	}
	@include breakFirst(large) {
		width: 25%;
	}
}


button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	background-position: 100% 0%;
}

button:focus,
input[type="button"]:focus,
input[type="reset"]:focus,
input[type="submit"]:focus,
button:active,
input[type="button"]:active,
input[type="reset"]:active,
input[type="submit"]:active {
	background-position: 100% 0%;
	outline: none;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	line-height: 1;
	padding: 15px 20px;
	border: none;
	margin: 0;
	display: block;
	float: left;
	height: 50px;
	width: calc(100% - 165px);
	@include breakFirst(medium) {
		width: 70%;
	}
	@include breakFirst(large) {
		width: 75%;
	}
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: darken($grayLight, 20);
}
::-moz-placeholder { /* Firefox 19+ */
  color: darken($grayLight, 20);
}
:-ms-input-placeholder { /* IE 10+ */
  color: darken($grayLight, 20);
}
:-moz-placeholder { /* Firefox 18- */
  color: darken($grayLight, 20);
}

select {
	border: 1px solid $grayLight;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
	outline: none;
}

textarea {
	width: 100%;
}