/*==============================================================
# Carousels
==============================================================*/

.carousel-section {
    padding: 100px 0;
    position: relative;
    &:before {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        height: 105%;
        background: linear-gradient(white 0%, rgba($gray, .2) 50%, white 100%);
        transform: translateY(-50%);
        z-index: -1;
    }
    &:after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        height: 105%;
        background-image: url("../img/backgrounds/gradient-pattern.png");
        background-position: center;
        transform: translateY(-50%);
        z-index: -2;
    }
    .candy-carousel {
        img {
            margin: 0 auto;
        }
    }
    .candy-slide {
        transition: all .4s $cbTrans;
        transform: scale(.65);
        position: relative;
        margin: 10px 20px 20px;
        padding: 10px 10px 30px;
        overflow: hidden;
        &.off-center {
            transform: scale(.85);
        }
        &.slick-center {
            transform: scale(1);
            &:after {
                transform: translate(-50%, 0);
            }
        }
        &:after {
            content: "";
            border-radius: 100%;
            height: 15px;
            width: 150%;
            max-width: 175px;
            position: absolute;
            left: 50%;
            bottom: 0px;
            background: radial-gradient(rgba($gray, .25) 0%, transparent 50%);
            transform: translate(-50%, 300%);
            transition: all .4s $cbTrans;
        }
    }
    .carousel-wrapper {
        position: relative;
        .pagination-button {
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);
            height: 100%;
            z-index: 100;
            vertical-align: middle;
            img {
                position: relative;
                top: 50%;
                transform: translateY(-50%);
            }
        }
        .previous-button {
            left: 0;
            @include breakFirst(medium) {
                left: -125px;
            }
        }
        .next-button {
            right: 0;
            @include breakFirst(medium) {
                right: -125px;
            }
        }
    }
}
.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
    outline: none;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}